import React from 'react';
import { StyleSheet } from 'react-native';
import {
  Layout,
  Select,
  SelectGroup,
  SelectItem,
} from '@ui-kitten/components';

export const SelectWithGroupsShowcase = () => {

  const [selectedIndex, setSelectedIndex] = React.useState();
  const [multiSelectedIndex, setMultiSelectedIndex] = React.useState();

  return (
    <Layout style={styles.container}>

      <Select
        style={styles.select}
        placeholder='Default'
        selectedIndex={selectedIndex}
        onSelect={setSelectedIndex}>
        <SelectGroup title='Group 1'>
          <SelectItem title='Option 1.1'/>
          <SelectItem title='Option 1.2'/>
        </SelectGroup>
        <SelectGroup title='Group 2'>
          <SelectItem title='Option 2.1'/>
          <SelectItem title='Option 2.2'/>
        </SelectGroup>
      </Select>

      <Select
        style={styles.select}
        multiSelect={true}
        placeholder='Multi'
        selectedIndex={multiSelectedIndex}
        onSelect={setMultiSelectedIndex}>
        <SelectGroup title='Group 1'>
          <SelectItem title='Option 1.1'/>
          <SelectItem title='Option 1.2'/>
        </SelectGroup>
        <SelectGroup title='Group 2'>
          <SelectItem title='Option 2.1'/>
          <SelectItem title='Option 2.2'/>
        </SelectGroup>
      </Select>

    </Layout>
  );
};

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    height: 320,
  },
  select: {
    flex: 1,
    margin: 8,
  },
});

