/**
 * IMPORTANT: To use Icon component make sure to follow this guide:
 * https://akveo.github.io/react-native-ui-kitten/docs/guides/icon-packages
 */

import React from 'react';
import {
  Icon,
  Layout,
  Menu,
  MenuItem,
} from '@ui-kitten/components';
import { StyleSheet } from 'react-native';

const StarIcon = (props) => (
  <Icon {...props} name='star'/>
);

const ForwardIcon = (props) => (
  <Icon {...props} name='arrow-ios-forward'/>
);

export const MenuWithIconsShowcase = () => {

  const [leftSelectedIndex, setLeftSelectedIndex] = React.useState();
  const [rightSelectedIndex, setRightSelectedIndex] = React.useState();

  return (
    <Layout style={styles.container}>

      <Menu
        style={styles.menu}
        selectedIndex={leftSelectedIndex}
        onSelect={setLeftSelectedIndex}>
        <MenuItem title='Users' accessoryLeft={StarIcon}/>
        <MenuItem title='Orders' accessoryLeft={StarIcon}/>
        <MenuItem title='Transactions' accessoryLeft={StarIcon}/>
      </Menu>

      <Menu
        style={styles.menu}
        selectedIndex={rightSelectedIndex}
        onSelect={setRightSelectedIndex}>
        <MenuItem title='Users' accessoryRight={ForwardIcon}/>
        <MenuItem title='Orders' accessoryRight={ForwardIcon}/>
        <MenuItem title='Transactions' accessoryRight={ForwardIcon}/>
      </Menu>

    </Layout>
  );
};

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    justifyContent: 'space-between',
  },
  menu: {
    flex: 1,
    margin: 8,
  },
});
