import React from 'react';
import { StyleSheet } from 'react-native';
import {
  Radio,
  Text,
} from '@ui-kitten/components';

const RadioTitle = (props) => (
  <Text {...props} style={[props.style, styles.text]}>
    Place your text
  </Text>
);

export const RadioInlineStylingShowcase = () => {

  const [checked, setChecked] = React.useState(false);

  return (
    <Radio
      style={styles.radio}
      text={RadioTitle}
      checked={checked}
      onChange={setChecked}
    />
  );
};

const styles = StyleSheet.create({
  radio: {
    width: 32,
    height: 32,
  },
  text: { color: '#3366FF' },
});
