import React, { useState } from 'react';
import { StyleSheet } from 'react-native';
import {
  Drawer,
  DrawerItem,
  IndexPath,
  Layout,
  Text,
} from '@ui-kitten/components';

const NotificationBadge = (props) => (
  <Layout {...props} style={[props.style, styles.badge]}>
    <Text appearance='alternative'>1</Text>
  </Layout>
);

export const DrawerNotificationBadgeItemShowcase = () => {

  const [selectedIndex, setSelectedIndex] = useState(new IndexPath(0));

  const onUsersPress = ({ index }) => {
    setSelectedIndex(index);
  };

  const onOrdersPress = ({ index }) => {
    setSelectedIndex(index);
  };

  return (
    <Drawer selectedIndex={selectedIndex}>
      <DrawerItem
        title='Users'
        onPress={onUsersPress}
      />
      <DrawerItem
        title='Orders'
        accessoryRight={NotificationBadge}
        onPress={onOrdersPress}
      />
    </Drawer>
  );
};

const styles = StyleSheet.create({
  container: {
    padding: 16,
  },
  badge: {
    justifyContent: 'center',
    alignItems: 'center',
    height: 24,
    width: 48,
    paddingHorizontal: 24,
    borderRadius: 12,
    backgroundColor: '#3366FF',
  },
});
