import React, { useState } from 'react';
import {
  ImageBackground,
  StyleSheet,
} from 'react-native';
import {
  Divider,
  Drawer,
  DrawerItem,
  IndexPath,
} from '@ui-kitten/components';

const Header = (props) => (
  <React.Fragment>
    <ImageBackground
      style={[styles.header, props.style]}
      source={require('../../assets/icon.png')}
    />
    <Divider/>
  </React.Fragment>
);

export const DrawerHeaderShowcase = () => {

  const [selectedIndex, setSelectedIndex] = useState(new IndexPath(0));

  const onUsersPress = ({ index }) => {
    setSelectedIndex(index);
  };

  const onOrdersPress = ({ index }) => {
    setSelectedIndex(index);
  };

  return (
    <Drawer selectedIndex={selectedIndex} header={Header}>
      <DrawerItem title='Users' onPress={onUsersPress}/>
      <DrawerItem title='Orders' onPress={onOrdersPress}/>
    </Drawer>
  );
};

const styles = StyleSheet.create({
  header: {
    height: 128,
    flexDirection: 'row',
    alignItems: 'center',
  },
});
